<template>
  <section>
    <div class="container mt-5">
        <router-link class="btn btn-success" to="/a/nuevo">Nuevo</router-link>
        <table class="table">
            <thead>
                <tr class="text-center">
                    <th scope="col">ID</th>
                    <th scope="col">animal</th>
                    <th scope="col">nombre cientifico</th>
                    <th scope="col">img</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="animal in animales" :key="animal.id" class="text-center">
                    <th scope="row">{{ animal.id }}</th>
                    <td><router-link :to="{ path: '/a/' + animal.id }">{{ animal.animal }}</router-link></td>
                    <td>{{ animal.nombreCientifico }}</td>
                    <td> <img :src="image + animal.img" :alt="animal.animal" class="img-animalList" /></td>
                </tr>
            </tbody>
        </table>
      </div>
  </section>
</template>

<script>
export default {
  data() {
      return {
          image: process.env.VUE_APP_ASSET_URL,
          animales: [],
      };
  },
  methods: {
      async getAnimales() {
          const res = await fetch(process.env.VUE_APP_API_URL + 'animal');
          const animales = await res.json();
          this.animales = animales.result;
      }
  },
  beforeMount() {
      this.getAnimales();
  }
}
</script>

<style>
    .img-animalList {
        width: 50px!important;
    }
</style>