import { createRouter, createWebHistory } from "vue-router";
import store from "@/store";

import Home from "@/pages/Home";
import Animales from "@/pages/animales/Animales";
import Random from "@/pages/animales/Animal";
import nuevoAnimal from "@/pages/animales/nuevo";
import escanearAnimal from "@/pages/animales/Escaner";
import verAnimal from "@/pages/animales/Ver";

import Usuarios from "@/pages/Usuarios";

const routes = [
	{
		path: "/",
		name: "Home",
		component: Home,
	},
	{
		path: "/a/nuevo",
		name: "NuevoAnimal",
		component: nuevoAnimal,
		meta: {
			rutaProtegida: true,
			roleAdmin: false,
		},
	},
	{
		path: "/a/escanear",
		name: "EscanearAnimal",
		component: escanearAnimal,
	},
	{
		path: "/a",
		name: "Animal",
		component: Animales,
	},
	{
		path: "/a/:id",
		name: "VerAnimal",
		component: verAnimal,
	},
	{
		path: "/r",
		name: "Random",
		component: Random,
	},
	{
		path: "/usuarios/",
		name: "Usuarios",
		component: Usuarios,
		meta: {
			rutaProtegida: true,
		},
	},
];

const router = createRouter({
	history: createWebHistory(process.env.BASE_URL),
	routes,
});

router.beforeEach((to, from, next) => {
	const rutaEsProtegida = to.matched.some((item) => item.meta.rutaProtegida);
	const rutaAdmin = to.matched.some((item) => item.meta.roleAdmin);
	if (
		(rutaEsProtegida && store.state.token === null) ||
		(rutaAdmin && store.state.usuarioDB.rol == 1)
	) {
		next("/");
	} else {
		next();
	}
});

export default router;
