<template>
  <section class="container">
    <div class="row pt-5">
      <div class="col-md-6 offset-md-3 col-sm-8 offset-2">
        <div class="card">
          <img :src="image + animal.img" class="card-img-top" :alt="animal.imgDesc">
          <div class="card-body pb-5">
              <h2 class="card-title text-center">{{ animal.animal }}</h2>
              <h5 class="card-subtitle mb-2 text-muted text-center">{{ animal.nombreCientifico }}</h5>
              <p class="card-text pb-5">{{ animal.descripcion }}</p>
              <div class="position-relative">
                 <div class="position-absolute top-50 start-50 translate-middle">
                  <a href="#" class="btn btn-primary p"><i class="bi bi-heart-fill"></i></a>
                 </div>
              </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
      return {
        image: process.env.VUE_APP_ASSET_URL,
        animal: {},
      };
  },
  methods: {
    async getRandom() {
      const res = await fetch(process.env.VUE_APP_API_URL + 'animal/random');
      const animal = await res.json();
      this.animal = animal.result[0];
    }
  },
  beforeMount() {
    this.getRandom();
  }
}
</script>

