<template>
  <section class="mt-5">
      <div class="container">
          <div class="alert" role="alert" v-if="estatus.estatus" :class="estatus.clase">
                {{ estatus.mensaje }}
          </div>
          <form @submit.prevent="escanearAnimal()">
              <div class="mb-3">
                <label for="imagen" class="form-label">Escanear imagen</label>
                <input type="file" accept="image/*" class="form-control" @change="onFileSelected">
              </div>
              <div class="mb-3">
                  <button type="submit" class="btn btn-success">Escanear</button>
              </div>
          </form>
      </div>
  </section>
</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            estatus: {
                estatus: false,
                mensaje: '',
                clase: ''
            },
            animal: {
                imagen: null
            }
        };
    },
    methods: {
        async escanearAnimal() {
            const formData = new FormData();
            formData.append('imagen', this.animal.imagen);
            
            axios.post(process.env.VUE_APP_API_URL + 'animal/escanear', formData)
            .then(result => {
                this.animal = result.data.result;
                this.$router.push('/a/' + this.animal.id);
            }).catch(error => {
                if(error.status == 404){
                    this.estatus = {
                        estatus: true,
                        mensaje: 'Error: especie no encontrada',
                        clase: 'alert-warning'
                    };
                }else{
                    this.estatus = {
                        estatus: true,
                        mensaje: 'Error al escanear, intente con otra imagen',
                        clase: 'alert-danger'
                    };
                }
            });
        },
        async onFileSelected(event) {
            this.animal.imagen = event.target.files[0];
        },
    },
}
</script>

