<template>
    <section>
        <Login v-if="!token"/>
        <Home v-else/>
    </section>
</template>

<script>
import { mapActions, mapState } from 'vuex';

import Login from "@/components/Login.vue";
import Home from "@/components/Home.vue";
export default {
    name: Home,
    components: {
        Login,
        Home
    },
    methods: {
        ...mapActions(['leerToken']),
    },
    created() {
        this.leerToken();
    },
    computed: {
        ...mapState(['token']),
    },
}
</script>