<template>
  <section class="mt-5">
      <div class="container">
          <div class="alert" role="alert" v-if="estatus.estatus" :class="estatus.clase">
                {{ estatus.mensaje }}
          </div>
          <form @submit.prevent="nuevoAnimal(animal)">
            <div class="mb-3">
                <label for="animal" class="form-label">animal</label>
                <input type="text" class="form-control" v-model="animal.animal">
            </div>
            <div class="mb-3">
                <label for="animal" class="form-label">nombre cientifico</label>
                <input type="text" class="form-control" v-model="animal.nombreCientifico">
            </div>
            <div class="mb-3">
                <label for="animal" class="form-label">descripcion</label>
                <textarea type="text" class="form-control" v-model="animal.descripcion"></textarea>
            </div>
            <div class="mb-3">
                <label for="animal" class="form-label">imagen</label>
                <input type="file" accept="image/*" @change="onFileSelected">
            </div>
            <button type="submit" class="btn btn-success">Crear</button>
          </form>
      </div>
  </section>
</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            animal: {
                id: 0,
                imagen: null
            },
            estatus: {
                estatus: false,
                mensaje: '',
                clase: ''
            },
        };
    },
    methods: {
        async nuevoAnimal(animal){
            const formData = new FormData();
            formData.append('imagen', this.animal.imagen);
            formData.append('animal', animal.animal);
            formData.append('nombreCientifico', animal.nombreCientifico);
            formData.append('descripcion', animal.descripcion);

            axios.post(process.env.VUE_APP_API_URL + 'animal/', formData)
            .then(() => {
                this.estatus = {
                    estatus: true,
                    mensaje: 'Especie guardada exitosamente',
                    clase: 'alert-success'
                }
                this.animal = {
                    animal: '',
                    nombreCientifico: '',
                    descripcion: '',
                    imagen: null
                };
            }).catch(error => {
                console.error(error);
                this.estatus = {
                    estatus: true,
                    mensaje: 'Error al guardar la especie: ' + error,
                    clase: 'alert-warning'
                }
            });
        },
        async onFileSelected(event) {
            this.animal.imagen = event.target.files[0];
        },
    }
}
</script>

