<template>
  <div class="container">
    <div class="row pt-5">
      <div class="col-md-6 offset-md-3 col-sm-8 offset-2">
        <form @submit.prevent="login(usuario)" v-if="login">
          <div class="mb-3">
              <label for="usuario" class="form-label">Usuario</label>
              <input type="text" class="form-control" v-model="usuario.usuario">
          </div>
          <div class="mb-3">
              <label for="password" class="form-label">Contraseña</label>
              <input type="password" class="form-control" v-model="usuario.contrasena">
          </div>
          <button type="submit" class="btn btn-success">Iniciar Sesión</button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { mapMutations, mapActions } from "vuex";
export default {
  data() {
    return {
      usuario: {

      },
      error: {
        estatus: false,
        mensaje: ""
      }
    }
  },
  methods: {
    ...mapMutations(['obtenerUsuario']),
    ...mapActions(['guardarUsuario', 'leerToken', 'cerrarSesion']),
    login() {
      axios.post(process.env.VUE_APP_API_URL + 'usuario/login', this.usuario)
      .then(result => {
        const token = result.data.token;
        this.guardarUsuario(token);
      }).catch(err => {
        console.error(err.message);
      });
    }
  }
}
</script>